import React from "react";
import {
  AppstoreOutlined,
  DatabaseOutlined,
  GiftOutlined,
  // ContainerOutlined,
  // DesktopOutlined,
  HomeOutlined,
  InboxOutlined,
  MailOutlined,
  //   MenuFoldOutlined,
  //   MenuUnfoldOutlined,
  // PieChartOutlined,
  // SettingOutlined,
} from "@ant-design/icons";
import { Menu } from "antd";
import { useNavigate } from "react-router-dom";
import "./Menu.scss";

function getItem(label, key, icon, children, type) {
  return {
    key,
    icon,
    children,
    label,
    type,
  };
}

const items = [
  getItem(
    "MAIN",
    "grp1",
    null,
    [getItem("Dashboard", "/dashboard", <HomeOutlined />)],
    "group"
  ),
  getItem(
    "WIDGETS",
    "grp2",
    null,
    [getItem("Widgets", "/home", <AppstoreOutlined />)],
    "group"
  ),
  getItem(
    "ELEMENTS",
    "grp3",
    null,
    [
      getItem("Components", "sub2", <DatabaseOutlined />, [
        getItem("Option 5", "/option-five"),
        getItem("Option 6", "/option-six"),
        getItem("Submenu", "sub3", null, [
          getItem("Option 7", "7"),
          getItem("Option 8", "8"),
        ]),
      ]),
      getItem("Elements", "sub4", <InboxOutlined />, [
        getItem("Option 9", "9"),
        getItem("Option 10", "10"),
        getItem("Option 11", "11"),
        getItem("Option 12", "12"),
      ]),
    ],
    "group"
  ),
  getItem("Navigation One", "sub1", <MailOutlined />, [
    getItem(
      "Item 1",
      "g1",
      null,
      [getItem("Option 1", "1"), getItem("Option 2", "2")],
      "group"
    ),
    getItem(
      "Item 2",
      "g2",
      null,
      [getItem("Option 3", "3"), getItem("Option 4", "4")],
      "group"
    ),
  ]),

  {
    type: "divider",
  },

  getItem(
    "PAGES",
    "grp5",
    null,
    [getItem("Pages", "13", <GiftOutlined />), getItem("Option 14", "14")],
    "group"
  ),
];
const MenuSet = () => {
  const navigate = useNavigate();
  // const [collapsed] = useState(false);
  // const toggleCollapsed = () => {
  //   setCollapsed(!collapsed);
  // };
  return (
    <div
      className="menuSet"
      //   style={{
      //     width: 256,
      //   }}
    >
      {/* <Button
        type="primary"
        onClick={toggleCollapsed}
        style={{
          marginBottom: 16,
        }}
      >
        {collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
      </Button> */}
      <Menu
        defaultSelectedKeys={["1"]}
        // defaultOpenKeys={["sub1"]}
        mode="inline"
        theme="dark"
        // inlineCollapsed={collapsed}
        items={items}
        // onClick={(info) => {
        //   console.log(info.key);
        // }}
        onClick={({ key }) => {
          if (key === "/") {
          } else {
            navigate(key);
          }
        }}
      />
    </div>
  );
};
export default MenuSet;
